/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.ba-header {
  background: #9f9acf url("../img/header-bg.png") 50% no-repeat;
  background-size: cover;
  width: 100%;
  min-height: 1070px;
  font-family: "Proxima Nova", sans-serif;
  color: #fff;
}

.ba-header-container {
  max-width: 81%;
  margin: 0 auto;
  padding: 36px 0.5% 0 1%;
}

.ba-header__logo-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 72px;
}

.ba-header__logo {
  align-self: flex-start;
}

.ba-header__info {
  text-align: center;
  padding: 230px 10px 10px;
  margin: 0 auto;
}

.ba-header__title h1 {
  font-family: "Proxima Nova", sans-serif;
  font-size: 72px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 15px;
  animation-delay: 0.5s;
}

.ba-header p {
  font-family: "PT Sans", sans-serif;
  font-size: 30px;
  font-style: italic;
  animation-duration: 0.8s;
  animation-delay: 1s;
}

.ba-logo:hover path {
  fill: #33FF99;
}

.ba-menu {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  padding-left: 0;
}

.ba-menu__item {
  margin-left: 4%;
}

.ba-menu__link {
  color: #fff;
  font-size: 24px;
  font-weight: 600;
  text-transform: uppercase;
  padding: 2px 5px;
}

.ba-menu__link:hover {
  color: #33FF99;
  border-top: 1px solid #33FF99;
  border-bottom: 4px solid #33FF99;
}

/*----Animation hamburger - begin----*/
#hamburger {
  float: right;
  width: 45px;
  height: 30px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

#hamburger span {
  display: block;
  position: absolute;
  height: 3px;
  width: 45px;
  background: #fff;
  opacity: 1;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

#hamburger span:nth-child(1) {
  top: 0;
}

#hamburger span:nth-child(2), #hamburger span:nth-child(3) {
  top: 14px;
}

#hamburger span:nth-child(4) {
  top: 27px;
}

#hamburger.ba-hamburger--open span:nth-child(1) {
  top: 14px;
  width: 0;
  left: 50%;
}

#hamburger.ba-hamburger--open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

#hamburger.ba-hamburger--open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

#hamburger.ba-hamburger--open span:nth-child(4) {
  top: 14px;
  width: 0;
  left: 50%;
}

/*----Animation hamburger - end----*/
/*---- Animated menu - begin -----*/
@media (min-width: 900px) {
  .ba-menu {
    display: flex;
  }
  .ba-menu #hamburger {
    display: none;
  }
